
@tailwind base;
@tailwind components;
@tailwind utilities;

@import '@fontsource/vt323';

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: 'Nunito', sans-serif;
  background-color: #1b1b1b;
  color: #b1b1b1;
}

#root {
  overflow-x: hidden;
}

/* Width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #4ade80;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #2e8b50;
}


@keyframes lead-the-way {
  0% {
      transform-origin: 0% 0%;
      transform: scale(1, 0);
  }
  50% {
      transform-origin: 0% 0%;
      transform: scale(1, 1);
  }
  50.1% {
      transform-origin: 0% 100%;
      transform: scale(1, 1);
  }
  100% {
      transform-origin: 0% 100%;
      transform: scale(1, 0);
  }
}

@keyframes blink {
  from { opacity: .2; }
  to { opacity: 0; }
}

@keyframes background-pan {
  from {
    background-position: 0% center;
  }
  to {
    background-position: -200% center;
  }
}

#trail {
  animation: lead-the-way 1.5s infinite;
}

#carrot {
  animation: blink 2s infinite;
}

.cool-box {
  aspect-ratio: 1;
  position: relative;
  height: 500px;
  width: calc(100% - 20px);
}

.cool-box-content {
  height: calc(100% - 2px);
  width: calc(100% - 2px);
  background-color: #1b1b1b;
}

.cool-box-content::before {
  content: '';
  height: 100%;
  left: 0px;
  top: 0px;
  position: absolute;
  width: 100%;
  background: radial-gradient(
    rgba(45, 136, 57, 0.2) 8%,
    transparent 15%
  );
  background-position: 50% 50%;
  background-size: 2vmin 2vmin;
  transition: background-size 500ms ease;
  transition-delay: 300ms;
}

.cool-box-content:hover::before {
  background-size: 1.8vmin 1.8vmin;
}

.cool-box::after {
  content: '';
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background: linear-gradient(to right,#3e5f47,#1b1b1b,#3e5f47);
  z-index: -1;
  animation: background-pan 10s linear infinite;;
  background-size: 200%;
}

li::before {
  content: 'â†’';
