  font-size: 16px;
  color: #4ade80;
}

.project-img {
  position: relative;
}

.project-img::after {
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.466);
}

@media (min-width: 768px) {
  .cool-box {
    width: 500px;
  }
}
